import Tool from "..";
import React, { useState } from 'react';
import useChatWithBot from "../../../hooks/useChatWithBot";
import dayjs from "dayjs";

function ChatWindow() {
  const [message, setMessage] = useState('');
  const {chat, history: chatHistory} = useChatWithBot();

  const handleSendMessage = async (e: any) => {
    e.preventDefault();
    if (message.trim() !== '') {
      await chat(message);
      setMessage('');
    }
  };

  const handleInputChange = (e: any) => {
    setMessage(e.target.value);
  };

  return (
    <div className="modal-mobile-fullscreen md:h-[500px] flex flex-col mt-4">
      <div className="overflow-y-scroll flex-auto">
        {chatHistory.map((message, index) => (
          <div
            key={index}
            className={`${
              message.sender === 'You' ? 'justify-end' : 'justify-start'
            } mb-2 flex`}
          >
            {message.sender !== 'You' && (
              <img
                src={message.avatar}
                alt={`${message.sender} avatar`}
                className="h-6 w-6 rounded-full mr-2"
              />
            )}
            <div
              className={`${
                message.sender === 'You'
                  ? 'bg-blue-500 text-white rounded-tr-lg rounded-bl-lg rounded-tl-lg'
                  : 'bg-gray-200 text-gray-800 rounded-tr-lg rounded-bl-lg rounded-br-lg'
              } p-2 max-w-xs`}
            >
              <div>{message.text}</div>
              <div className="text-xs text-gray-600">{`${message.sender} - ${dayjs(message.timestamp).format()}`}</div>
            </div>
          </div>
        ))}
      </div>
      <form className="flex items-center bg-gray-100 p-2">
        <input
          type="text"
          placeholder="Type a message..."
          value={message}
          onChange={handleInputChange}
          className="flex-1 bg-white rounded-full py-2 px-4 mr-2 focus:outline-none focus:shadow-outline"
        />
        <button
          type="submit"
          onClick={handleSendMessage}
          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-full focus:outline-none focus:shadow-outline"
        >
          Send
        </button>
      </form>
    </div>
  );
}


const ChatGPTTool = () => {

    return (
        <Tool title="GPT聊天" modalWidth="md:w-[60%]">
            <ChatWindow />
        </Tool>
    )
}

export default ChatGPTTool;